.products{
    display: flex;
    flex-direction: column;
    gap: 2vw;
    align-items: center;
}

.productcard{
    height: 30vw;
    width: 20vw;
    border: 1px solid rgb(229, 229, 229);
    border-radius: 10px;
    padding: 2%;
    font-size: 1.4vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2%;
}

.productcard img{
    width: 50%;
}

.items{
    display: flex;
    gap: 48px;
    flex-wrap: wrap;
    align-items: center;
}

.add-btn{
    color: white;
    width: 100%;
    height: 4vw;
}

.info{
    padding: 5%;
    display: flex;
    flex-direction: column;
    gap: 1vw;
    align-items: flex-start;
    justify-content: flex-start;
}

@media screen and (max-width:600px)
{
    .items{
        display: flex;
        flex-direction: column;
        gap: 48px;
        align-items: center;
    }

    .productcard{
        height: 62vw;
    width: 60vw;
    border: 1px solid rgb(229, 229, 229);
    border-radius: 10px;
    padding: 2%;
    font-size: 2.4vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2%;
    }

    .productcard img{
        width: 41%;
    }

    .info{
        padding: 3%;
        display: flex;
        flex-direction: column;
        gap: 1vw;
        align-items: flex-start;
        justify-content: flex-start;
    }

    .add-btn{
        color: white;
        width: 53vw;
        height: 6vw;
    }
}